.film {
  position: relative;
  background: rgb(1, 1, 65);
  width: 300px;
  height: 750px;
  text-align: left;
  border-radius: 50px;
  padding: 10px;
  margin: 10px;
}
.film img {
  max-width: 250px;
  border-radius: 30px;
  width: 100%;
  margin: auto;
  display: block;
}
.film h2 {
  color: white;
  font-size: 1rem;
}
.film .date-sortie {
  color: grey;
  letter-spacing: 1px;
}
.film .note-average {
  color: white;
  font-weight: bold;
}
.film .star {
  color: yellow;
}
.film .genre ul {
  display: flex;
  justify-content: left;
}
.film .genre li {
  background: rgb(54, 52, 54);
  margin: 5px;
  color: white;
  border-radius: 10px;
  padding: 5px;
}
.film h3 {
  color: white;
}
.film .description {
  color: grey;
  font-size: 0.8rem;
  line-height: 1.2rem;
}

.film .ajouter-coups-de-coeur {
  color: white;
  background: rgb(2, 146, 79);
  width: 200px;
  height: 20px;
  font-size: 0.8rem;
  border-radius: 15px;
  margin: 10px auto;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
}
.film .enlever-coups-de-coeur {
  color: white;
  background: rgb(89, 2, 129);
  width: 200px;
  height: 20px;
  font-size: 0.8rem;
  border-radius: 15px;
  margin: 10px auto;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
}
