.header {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.titre {
  color: white;
  font-size: 1rem;
}
.search {
  display: flex;
  flex-direction: column;
  width: 33%;
  max-width: 400px;
  min-width: 200px;
  margin: 0 auto;
  border: 2px solid rgb(1, 1, 19);
  padding: 20px 20px 10px;
  border-radius: 30px;
  background: rgb(15, 1, 68);
}
.search input[type="text"] {
  text-align: center;
  height: 35px;
  border-radius: 20px 20px 0 0;
  border: none;
  color: gray;
}
.search button {
  color: white;
  background-color: navy;
  height: 35px;
  border-radius: 0 0 20px 20px;
}
.search input:focus {
  border: none;
  color: black;
}
.top-flop {
  background: navy;
  border-radius: 20px;
  margin: 10px;
  display: flex;
  font-weight: bold;
  justify-content: space-around;
  cursor: pointer;
  color: white;
}
.cards-container {
  display: flex;
  flex-wrap: wrap;
}
.cards-container p {
  display: block;
  color: white;
  margin: 0 auto;
}
